
.content {
    background-color: rgb(45, 45, 45);
    box-sizing: border-box;
    display: block;
    font-family: "Open Sans", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
    height: 729.6px;
    line-height: 18.4px;
    padding: 19px 23px;
    position: relative;
    text-size-adjust: 100%;
  }
  
.block {
    color: #FFFFFF;
    font-weight: 700;
    margin-bottom: 17px;
  }
  
.blocks {
    width: 278px;
    height: 49px;
    display: flex;
    align-items: center;
    border-radius: 4px;
    background-color: #FFFFFF;
    margin-bottom: 10px;
    font-weight: 300;
    position: relative;
  }
  
.icon {
    margin-right: 10px; 
  }
  
.grip {
    width: 11px; 
    height: 18px;
    top: 15px;
    left: 15px;
    padding: 5px;
    padding-left: 10px;

  }
  
.label {
    font-size: 16px;
    line-height: 22px;
    color: #000000;
    position: absolute;
    left: 30px; 
    top: 50%; 
    transform: translateY(-50%); 
  }

  .export-button {
    padding: 12px;
    background-color: #007bff;
    color: white;
    border: none;
    cursor: pointer;
    font-size: 16px;
    width: 100%;
    white-space: nowrap;
    /* margin-left: 45%; */
    margin-top: 157%;
  }
  
  .export-button:hover {
    background-color: #0056b3;
  }
